<template>
  <div class="map-index">
    <iframe
      src="https://apis.map.qq.com/tools/locpicker?search=1&type=1&key=GTZBZ-RVN64-XVMUP-XP4FH-CDBNV-HEFKF&referer=myapp&coord=29.9499,121.4641"
      frameborder="0"
      width="100%"
      height="100%"
    ></iframe>
  </div>
</template>

<script>
export default {
  mounted() {
    window.addEventListener("message", this.handleMessage, false);
  },
  methods: {
    handleMessage(event) {
      // 接收位置信息，用户选择确认位置点后选点组件会触发该事件，回传用户的位置信息
      var loc = event.data;
      if (loc && loc.module == "locationPicker") {
        //防止其他应用也会向该页面post信息，需判断module是否为'locationPicker'
        console.log("location", loc);
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.map-index {
  height: 100vh;
}
</style>
